<template>
    <div id="my">
        <van-image :src="require('@/assets/my-bg.png')" width="360px" height="85px"/>
        <van-button to="/login" v-show="hid">请先登录</van-button>
        <div class="user-img" v-show="sho"></div>
        <p id="name" v-show="sho">{{name}}</p>
        <van-grid :column-num="3">
            <van-grid-item v-for="val in card" :icon="val.src" :text="val.title" :to="val.to"/>
        </van-grid>
    </div>
</template>

<script>
    export default{
        data(){
            return{
                name:localStorage.getItem('name'),
                hid:true,
                sho:false,
                card:[
                    {
                        src:require('@/assets/shoucang.svg'),
                        title:'收藏',
                        to:''
                    },
                    {
                        src:require('@/assets/pinlun.svg'),
                        title:'评价',
                        to:''
                    },
                    {
                        src:require('@/assets/liulang.svg'),
                        title:'浏览',
                        to:''
                    },
                    {
                        src:require('@/assets/qianbao.svg'),
                        title:'钱包',
                        to:''
                    },
                    {
                        src:require('@/assets/kaquan.svg'),
                        title:'红包/卡券',
                        to:'/coupons'
                    },
                    {
                        src:require('@/assets/yue.svg'),
                        title:'余额',
                        to:''
                    }
                ],
            }
        },
        methods:{
            getName(){
                if(this.name){
                    this.hid = false;
                    this.sho = true;
                }
            }
        },
        beforeMount: function () {
           this.getName();
        }
    }
</script>

<style scoped lang="less">
    #my{
        width: 360px;
        height:640px;
        position: relative;
        div.user-img{
            width: 64px;
            height: 64px;
            background-image: url(../assets/my-user.jpeg);
            background-repeat: no-repeat;
            background-size: 64px;
            background-position: 0 -10px;
            border-radius: 33px;
            position: absolute;
            top: 52px;
            left: 148px;
        }
        .van-button{
            position: relative;
            left: 50%;
            transform: translatex(-50%);
        }
        p{
            padding-top: 30px;
            padding-bottom: 10px;
            text-align: center;
        }
    }
</style>
