<template>
    <div id="registered">
        <div id="backPage">
            <router-link to="/home">
                <div>跳过</div>
            </router-link>
        </div>
        <van-form @submit="onSubmit">
          <van-field
            v-model="name"
            name="用户名"
            label="用户名"
            placeholder="用户名"
            :rules="[{ required: true, message: '请填写用户名' }]"
            onkeyup="value=value.replace(/[/^[a-zA-Z_]\w{5,11}$]/)"
          />
          <van-field
            v-model="passWord"
            type="password"
            name="密码"
            label="密码"
            placeholder="密码"
            :rules="[{pattern,required: true, message: '请正确填写密码'}]"
          />
          <van-field
            v-model="passWordTwo"
            type="password"
            name="确认密码"
            label="确认密码"
            placeholder="确认密码"
            :rules="[{required: true, message: '请正确填写确认密码' }]"
          />
          <div class="btn btnOne">
              <van-button block color="#ff8d1a" to="/login" plain>登录</van-button>
            </div>
            <div class="btn">
              <van-button block color="#ff8d1a" native-type="submit">注册</van-button>
            </div>
          </van-form>
        </van-form>
    </div>
</template>

<script>
    export default {
      data() {
        return {
          name: '',
          passWord: '',
          passWordTwo:'',
          pattern:/^[0-9A-Za-z]{6,12}$/,
        };
      },
      methods: {
        onSubmit(){
          if(this.passWord == this.passWordTwo){
              localStorage.setItem('name',this.name);
              localStorage.setItem('password',this.passWord);
              this.$router.push({path:'/login',query:{name:this.name,password:this.passWord}});
          }else{
              if(this.passWord !== this.passWordTwo){
                  alert('确认密码不同');
              }
          }
        },
      },
    };
</script>

<style scoped lang="less">
    #registered{
        width: 360px;
        height: 640px;
        background-image: url(../assets/xdatls.jpg);
        background-repeat: no-repeat;
        background-size: 100%;
        position: relative;
        #backPage{
            position: relative;
            div{
                height: 30px;
                width: 50px;
                color: rgba(80, 80, 80, 1);
                line-height: 30px;
                font-size: 14px;
                text-align: center;
                position: absolute;
                right: 15px;
                top: 18px;
            }
        }
        form{
            position: absolute;
            top: 229px;
            div.van-cell{
                width: 220px;
                height: 40px;
                margin: 0px 60px 10px 80px;
            }
        }
        div.btn{
            width: 100px;
            height: 40px;
            float: left;
        }
        div.btnOne{
            margin-left: 80px;
            margin-right: 19px;
        }
    }
</style>
