<template>
    <div id="tijiao">
        <div class="head">
            <Fanhui/>
        </div>
        <van-row class="taoc">
            <van-col span="4" offset="1">
                <div class="img"></div>
            </van-col>
            <van-col span="15"  offset="1">
                <h4>{{name}}</h4>
                <p>周一至周日</p>
                <p>免预约  |  随时退  |  过期自动退</p>
            </van-col>
            <van-col span="2">¥95</van-col>
        </van-row>
        <van-row class="shu">
          <van-col span="10" offset="1">数量</van-col>
          <van-col span="7" offset="6">
              <van-stepper v-model="value" integer theme="round" button-size="22"/>
          </van-col>
        </van-row>
        <van-row class="shu">
          <van-col span="10" offset="1">小计</van-col>
          <van-col span="3" offset="10">
                {{JiaGe}}
          </van-col>
        </van-row>
        
        <!-- 优惠券单元格 -->
        <van-coupon-cell
          :coupons="coupons"
          :chosen-coupon="chosenCoupon"
          @click="showList = true"
        />
        <!-- 优惠券列表 -->
        <van-popup
          v-model="showList"
          round
          position="bottom"
          style="height: 90%; padding-top: 4px;"
        >
          <van-coupon-list
            :coupons="coupons"
            :chosen-coupon="chosenCoupon"
            :disabled-coupons="disabledCoupons"
            @change="onChange"
            @exchange="onExchange"
          />
        </van-popup>
        
        <van-row class="shu">
          <van-col span="10" offset="1">实付金额</van-col>
          <van-col span="3" offset="10">
                {{Shiji}}
          </van-col>
        </van-row>
        <van-submit-bar :price="tprice" button-text="提交订单" @submit="onSubmit" />
        <van-popup v-model="show" position="bottom" :style="{ height: '424px' }" closeable close-icon-position="top-left">
            <p>确认付款</p>
            <h2>{{price | changNum}}</h2>
            <van-row>
                <van-col span="6" offset="1">订单信息</van-col>
                <van-col span="10" offset="7">小鲍糕点站{{this.taocan[this.$route.query.id].tcname}}</van-col>
            </van-row>
            <van-row>
                <van-col span="6" offset="1">付款方式</van-col>
                <van-col span="4" offset="13">余额宝&gt;</van-col>
            </van-row>
            <van-row>
                <van-col span="8" offset="1">花呗最高立减10元</van-col>
                <van-col span="4" offset="10">立即使用</van-col>
            </van-row>
            <van-button type="primary" block color="#ff8d1a" @click="fukuan()">立即付款</van-button>
        </van-popup>
    </div>
</template>

<script>
    import Fanhui from "@/components/fanhui"
    const coupon = {
      available: 1,
      condition: '无使用门槛\n最多优惠12元',
      reason: '',
      value: 150,
      startAt: 1489104000,
      endAt: 1514592000,
      valueDesc: '1.5',
      unitDesc: '元',
    };
    export default{
        components:{
            Fanhui
        },
        data(){
            return{
                id:this.$route.query.id,
                name:'',
                value:1,
                price:0,
                src:'',
                zhifu:0,
                tprice:0,
                shiji:0,
                show: false,
                chosenCoupon: -1,
                coupons: [coupon],
                disabledCoupons: [coupon],
                showList:false,
                PurchasedProductInformation:{},
                taocan:[
                    {imgsrc:require('@/assets/yirentc.jpg'),tcname:'1人独享套餐'},
                    {imgsrc:require('@/assets/tworen.jpg'),tcname:'2人招牌套餐'},
                    {imgsrc:require('@/assets/yirentc.jpg'),tcname:'4人特惠套餐'}
                ]
            };
        },
        computed:{
            JiaGe(){
                this.price = this.value*95;
                this.tprice = this.value*95*100;
                return this.price;
            },
            Shiji(){
                if(this.chosenCoupon == 1){
                    this.shiji = this.price - coupon.valueDesc;
                }else{
                    this.shiji = this.price;
                }
                return this.shiji;
            }
        },
        methods: {
            onChange(index) {
                this.showList = false;
                this.chosenCoupon = index;
            },
            onExchange(code) {
                this.coupons.push(coupon);
            },
            onSubmit(){
                this.show = true;
            },
            fukuan(){
                this.zhifu = 1;
                this.$set(this.PurchasedProductInformation,'name',this.name);
                this.$set(this.PurchasedProductInformation,'price',this.price);
                this.$set(this.PurchasedProductInformation,'zhifu',this.zhifu);
                this.$set(this.PurchasedProductInformation,'dingdanhao','订单号:23847563928174');
                this.$set(this.PurchasedProductInformation,'Src',this.src);
                this.$set(this.PurchasedProductInformation,'num',this.value);
                localStorage.setItem('PurchasedProductInformation',JSON.stringify(this.PurchasedProductInformation));
                this.$router.push({path:'/purchase'});
            }
        },
        filters:{
            changNum(price){
                if(price){
                    var noNegative = true;
                    var s=(price + "").replace(/[^\d\.-]/g, ""); //把数字和. 换成空格,也就是前面均加上""
                    //此时，转换后的值为 12345678.234   数字与数字之间, .之间，均有"" 
                    s=parseFloat(s).toFixed(2); //先转换成小数，后四舍五入两位小数
                    s=s+""; //转换成字符串
                    if(parseFloat(s) < 0){ //是负数
                        s = Math.abs(s).toFixed(2) + "";  //先转换成正数，然后取两位小数
                        noNegative = false; //标志位为负数
                    }
                    var zheng=s.split(".")[0]; //取出前面的整数值。
                    var zhengArr=zheng.split("").reverse(); //先按照""进行拆分，后进行反转。
                    var dian=s.split(".")[1]; //取出小数部分，为23
                    var t="";
                    for(var i=0;i<zhengArr.length;i++){
                        if(i%3==2&&i!=zhengArr.length-1){ //为第三位
                            t+=zhengArr[i]+",";
                        }else{
                            t+=zhengArr[i]+""; //加上空格
                        }
                    }
                    
                    //进行拼接， 符号位+ 反转后的数字+小数点+小数部分
                    var value= '￥'+(noNegative?"":"-")+t.split("").reverse().join("")+"."+dian+'元';
                    
                    return value;
                }else{
                    return '0';
                }
              
            }
        },
        beforeMount:function(){
            this.name = this.taocan[this.id].tcname;
            this.src = this.taocan[this.id].imgsrc;
        }
    }
</script>

<style scoped lang="less">
    #tijiao{
        div.head{
            width: 360px;
            height: 48px;
            border-bottom: 1px solid #f3f3f3;
        }
        div.taoc{
            width: 360px;
            height: 80px;
            border-bottom: 1px solid #f3f3f3;
            padding-top: 10px;
            box-sizing: border-box;
            div{
                color: rgba(166, 166, 166, 1);
                font-size: 11px;
                div.img{
                    width: 50px;
                    height: 50px;
                    background-image: url(../assets/mhkk.jpg);
                    background-repeat: no-repeat;
                    background-size: 50px;
                }
                h4{
                    color: rgba(56, 56, 56, 1);
                    font-size: 14px;
                }
                p{
                    color: rgba(166, 166, 166, 1);
                    font-size: 11px;
                }
            }
        }
        div.shu{
            width: 360px;
            height: 38px;
            padding: 5px 0px;
            border-bottom: 1px solid #f3f3f3;
            line-height: 38px;
            div{
                color: rgba(56, 56, 56, 1);
                font-size: 14px;
            }
        }
        div.van-popup{
            p{
                text-align: center;
                height: 50px;
                line-height: 50px;
                color: rgba(0, 0, 0, 0.7735849056603773);
                font-size: 20px;
            }
            h2{
                font-size: 34px;
                text-align: center;
            }
            div.van-row{
                padding: 10px 0px;
                border-bottom: 1px solid #f3f3f3;
                color: rgba(56, 56, 56, 1);
                font-size: 14px;
                text-align: left;
                div.van-col--8{
                    font-weight: bold;
                }
                div.van-col--10{
                    font-weight: bold;
                }
            }
            button{
                width: 330px;
                height: 45px;
                position: absolute;
                bottom: 15px;
                left: 15px;
            }
        }
    }
</style>
