<template>
    <div id="sou">
        <Fanhui/>
        <form action="/">
            <van-search
            v-model="value"
            show-action
            placeholder="请输入搜索关键词"
            @search="onSearch"
            >
                <template #action>
                    <div @click="onSearch">搜索</div>
                </template>
            </van-search>
        </form>
        <div class="rem">
            <h5>热门搜索</h5>
            <div>
                <span>高新区</span>
                <span>高新文体店</span>
                <span>高新去文体中心</span>
                <span>虎丘区</span>
            </div>
        </div>
        <div class="his">
            <h5>历史记录</h5>
        </div>
    </div>
</template>

<script>
    import Fanhui from '@/components/fanhui'
    import { Toast } from 'vant';
    export default {
      data() {
        return {
          value: '',
        };
      },
      methods: {
        onSearch(){
            let div = document.createElement('div');
            div.setAttribute('class','nei');
            let his = document.getElementsByClassName('his')[0];
            div.innerHTML = this.value;
            his.appendChild(div);
            this.value = '';
        }
      },
      components:{
          Fanhui
      }
    };
</script>

<style scoped lang="less">
