<template>
    <div id="login">
        <div id="backPage">
            <router-link to="/home">
                <div>跳过</div>
            </router-link>
        </div>
        <van-form @submit="onSubmit">
          <van-field
            v-model="name"
            name="用户名"
            label="用户名"
            placeholder="用户名"
            :rules="[{ required: true, message: '请填写用户名' }]"
          />
          <van-field
            v-model="passWord"
            type="password"
            name="密码"
            label="密码"
            placeholder="密码"
            :rules="[{ required: true, message: '请填写密码' }]"
          />
          <div class="btn btnOne">
            <van-button block color="#ff8d1a" to="/registered" plain>注册</van-button>
          </div>
          <div class="btn">
            <van-button block color="#ff8d1a" native-type="submit">登录</van-button>
          </div>
        </van-form>
    </div>
</template>

<script>
    // 提示弹框
    import { Dialog } from 'vant';
    export default {
      data() {
        return {
          name:this.$route.query.name,//获取输入的用户名
          passWord:this.$route.query.password,//获取输入的密码
          localName:localStorage.getItem('name'),//获取缓存的用户名
          localPassWord:localStorage.getItem('password'),//获取缓存的密码
        };
      },
      methods: {
        onSubmit(values) {
           if(Object.is(this.localName,null)){
               Dialog({ message: '请先注册账号' });
               return false;
           }else if(this.name !== this.localName || this.passWord !== this.localPassWord){
               Dialog({ message: '用户名不正确或密码不正确' });
               console.log(this.name);
               return false;
           }else{
               this.$router.push({path:'/home'})
           }
        },
      },
    };
</script>

<style scoped lang="less">
    #login{
        width: 360px;
        height: 640px;
        background-image: url(../assets/xdatls.jpg);
        background-repeat: no-repeat;
        background-size: 100%;
        position: relative;
        #backPage{
            position: relative;
            div{
                height: 30px;
                width: 50px;
                color: rgba(80, 80, 80, 1);
                line-height: 30px;
                font-size: 14px;
                text-align: center;
                position: absolute;
                right: 15px;
                top: 18px;
            }
        }
        form{
            position: absolute;
            top: 229px;
            div.van-cell{
                width: 220px;
                height: 40px;
                margin: 0px 60px 10px 80px;
            }
        }
        div.btn{
            width: 100px;
            height: 40px;
            float: left;
        }
        div.btnOne{
            margin-left: 80px;
            margin-right: 19px;
        }
    }
</style>
