import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import Nearby from '../views/nearby'
import Find from '../views/find'
import Order from '../views/Order'
import First from '../views/first'
import FirstTwo from '../views/firstTwo'
import FirstThree from '../views/firstThree'
import FirstFour from '../views/firstFour'
import FirstFive from '../views/firstFive'
import Ind from '../views/ind'
import My from '../views/My'
import Sou from '../views/sou'
import Xbgdz from '../views/xbgdz'
import Buy from '../views/Buy'
import Tijiao from '../views/Tijiao'
import Login from '../views/Login'
import Registered from '../views/Registered'
import Coupons from '../views/Coupons'
import Purchase from '../views/Purchase'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: First
    },
    {
        path: '/firstTwo',
        component: FirstTwo
    },
    {
        path: '/firstThree',
        component: FirstThree
    },
    {
        path: '/firstFour',
        component: FirstFour
    },
    {
        path: '/firstFive',
        component: FirstFive
    },
    {
        path: '/home',//默认路径
        component: Home,
        children:[
            {
                path: '',
                component: Ind
            },
            {
                path: '/home/nearby',
                component: Nearby
            },
            {
                path: '/home/find',
                component: Find
            },
            {
                path: '/home/order',
                component: Order
            },
            {
                path: '/home/my',
                component: My
            }
        ]
    },
    {
        path: '/sou',
        component: Sou
    },
    {
        path: '/xbgdz',
        component: Xbgdz,
    },
    {
        path: '/buy',
        component: Buy
    },
    {
        path: '/tijiao',
        component: Tijiao
    },
    {
        path:'/login',
        component:Login
    },
    {
        path:'/registered',
        component:Registered
    },
    {
        path:'/coupons',
        component:Coupons
    },
    {
        path:'/purchase',
        component:Purchase
    }
    
]

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

export default router
